@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary: #3182ce;
  --secondary: #805ad5;
  --secondary-light: #faf5ff;
  --accent: #ed64a6;
  --accent-light: #fff5f7;
  --light: #f7fafc;
  --dark: #1a202c;
}

html {
  font-size: 10px;
}

body {
  font-family: "Open Sans", sans-serif;
  height: 100%;
}

ul {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

.btn {
  padding: 1rem 2rem;
  background: none;
  font-size: 1.7rem;
  border: none;
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 0.15rem;
  transition: all 0.3s ease-out;
}

.main {
  background: var(--light);
  width: 90%;
  height: 100%;
  min-height: 75vh;
  margin: 2rem auto;
  padding: 1rem;
  box-shadow: 2px 2px 1px 4px rgba(247, 149, 209, 0.3);
}

@media (min-width: 500px) {
  .main {
    width: 80%;
  }
}

@media (min-width: 745px) {
  .main {
    width: 50%;
  }
}

@media (min-width: 1024px) {
  .main {
    width: 30%;
  }
} ;

ul {
  width: 100%;
  height: 100%;
}

.todo_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--dark);
  margin-bottom: 0.5rem;
}

.todo_item span {
  font-size: 1.7rem;
  letter-spacing: 0.13rem;
  padding: 0.5rem 0.8rem;
  color: var(--light);
  text-align: center;
}

.btn--delete {
  background: var(--accent);
  color: var(--accent-light);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 1.5rem;
  text-transform: uppercase;
}

form {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid var(--dark);
  padding-bottom: 2rem;
  margin-bottom: 1rem;
}

.input_container {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}

label {
  font-size: 1.7rem;
  text-transform: uppercase;
  letter-spacing: 0.15rem;
  margin-bottom: 0.8rem;
}

input[type="text"] {
  padding: 0.6rem 1rem;
  font-size: 1.5rem;
  letter-spacing: 0.12rem;
  font-family: inherit;
  border: 1px solid var(--primary);
}

.btn--submit {
  background: var(--secondary);
  color: var(--secondary-light);
  border-bottom: 10px solid #b794f4;
}

.btn--submit:hover {
  background: var(--accent);
  border-color: #f687b3;
  color: var(--accent-light);
}
.btn--submit:hover,
.btn--submit:active,
.btn--submit:focus {
  transform: translateY(0.3rem);
}

